
// src/app/api/admin/site-settings/telegram/test-message/route.ts
import { NextRequest, NextResponse } from 'next/server';
import { query } from '@/lib/mysql';
import { sendTelegramMessage } from '@/lib/telegram'; 
import type { SiteTelegramSettings } from '@/types';

const SETTINGS_ROW_ID = 1;

export async function POST(request: NextRequest) {
  try {
    // TODO: Add proper admin authentication check here
    const { botType, message } = await request.json();

    if (!botType || !message || !['client', 'admin', 'key'].includes(botType)) { // Updated this line
      return NextResponse.json({ message: '–ù–µ–∫–æ—Ä—Ä–µ–∫—Ç–Ω—ã–π —Ç–∏–ø –±–æ—Ç–∞ –∏–ª–∏ –æ—Ç—Å—É—Ç—Å—Ç–≤—É–µ—Ç —Å–æ–æ–±—â–µ–Ω–∏–µ.' }, { status: 400 });
    }

    const settingsResults = await query('SELECT * FROM site_telegram_settings WHERE id = ? LIMIT 1', [SETTINGS_ROW_ID]);
    if (!Array.isArray(settingsResults) || settingsResults.length === 0) {
      return NextResponse.json({ message: '–ù–∞—Å—Ç—Ä–æ–π–∫–∏ Telegram –Ω–µ –Ω–∞–π–¥–µ–Ω—ã.' }, { status: 404 });
    }
    const config: SiteTelegramSettings = settingsResults[0];

    let token: string | null | undefined;
    let chatIdInput: string | null | undefined;

    switch(botType) {
        case 'client':
            token = config.client_bot_token;
            chatIdInput = config.client_bot_chat_id;
            break;
        case 'admin':
            token = config.admin_bot_token;
            chatIdInput = config.admin_bot_chat_ids;
            break;
        case 'key': // Added case for key bot
            token = config.key_bot_token;
            chatIdInput = config.key_bot_admin_chat_ids;
            break;
        default:
            // This case should not be reached due to the initial validation
            return NextResponse.json({ message: '–ù–µ–∏–∑–≤–µ—Å—Ç–Ω—ã–π —Ç–∏–ø –±–æ—Ç–∞.' }, { status: 400 });
    }


    if (!token) {
      return NextResponse.json({ message: `–¢–æ–∫–µ–Ω –¥–ª—è ${botType} –±–æ—Ç–∞ –Ω–µ –Ω–∞—Å—Ç—Ä–æ–µ–Ω.` }, { status: 400 });
    }
    if (!chatIdInput) {
        return NextResponse.json({ message: `ID —á–∞—Ç–∞(–æ–≤) –¥–ª—è ${botType} –±–æ—Ç–∞ –Ω–µ –Ω–∞—Å—Ç—Ä–æ–µ–Ω.` }, { status: 400 });
    }
    
    const chatIds = chatIdInput.split(',').map(id => id.trim()).filter(id => id);
    if (chatIds.length === 0) {
        return NextResponse.json({ message: `–ù–µ —É–∫–∞–∑–∞–Ω—ã –∫–æ—Ä—Ä–µ–∫—Ç–Ω—ã–µ ID —á–∞—Ç–∞(–æ–≤) –¥–ª—è ${botType} –±–æ—Ç–∞.` }, { status: 400 });
    }

    let allSentSuccessfully = true;
    let firstError = null;

    for (const chatId of chatIds) {
        const result = await sendTelegramMessage(token, chatId, message);
        if (!result.success) {
            allSentSuccessfully = false;
            if (!firstError) firstError = result.error || 'Unknown error during sending.';
            console.error(`Failed to send test message to ${chatId} for ${botType} bot:`, result.error);
        }
    }

    if (allSentSuccessfully) {
      return NextResponse.json({ message: `–¢–µ—Å—Ç–æ–≤–æ–µ —Å–æ–æ–±—â–µ–Ω–∏–µ —É—Å–ø–µ—à–Ω–æ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω–æ –Ω–∞ ${chatIds.join(', ')} —á–µ—Ä–µ–∑ ${botType} –±–æ—Ç–∞.` });
    } else {
      return NextResponse.json({ message: `–ù–µ —É–¥–∞–ª–æ—Å—å –æ—Ç–ø—Ä–∞–≤–∏—Ç—å —Ç–µ—Å—Ç–æ–≤–æ–µ —Å–æ–æ–±—â–µ–Ω–∏–µ –Ω–∞ –æ–¥–∏–Ω –∏–ª–∏ –Ω–µ—Å–∫–æ–ª—å–∫–æ —á–∞—Ç–æ–≤. –ü–µ—Ä–≤–∞—è –æ—à–∏–±–∫–∞: ${firstError}` }, { status: 500 });
    }

  } catch (error: any) {
    console.error('API Send Test Telegram Error:', error);
    return NextResponse.json({ message: `–í–Ω—É—Ç—Ä–µ–Ω–Ω—è—è –æ—à–∏–±–∫–∞ —Å–µ—Ä–≤–µ—Ä–∞: ${error.message}` }, { status: 500 });
  }
}

    